import React, {useEffect,useState, Component} from 'react'
import './users.css'


class User extends Component{
  

  state = {
    user: []
  }

  getUsers=(e)=>{

    e.preventDefault()
    
    if(!this.props.param ){
      alert('Please login before')
    }else{
      fetch('http://127.0.0.1:8000/api/users/')
      .then(data=>data.json())
      .then(data=>{
        this.setState({user:data})
      })
      .catch(err=>{console.log(err)})
      
    }
    
    

  }
  
    render(){
        return(
            <div className='users-list'>
              <button onClick={this.getUsers} className='getBtn'>Все Пользователи</button>
               {
                 this.state.user.map(x=>{
                   return(
                     <div className='user-item' key={x.id}>
                        <h3>ФИО: {x.name}</h3>
                        <p>Телефон: {x.phone}</p>
                        <p>Адрес: {x.adress}</p>
                        <p>ИНН: {x.id_num}</p>
                     </div>
                   )
                 })
               } 
            </div>
      )
    }    
 
}
export default User