import React, { Component} from 'react'
import './form.css'
import { Redirect } from 'react-router-dom';


class Signin extends Component{

  
  
  state = {
    user : {
      username : '',
      password : ''
    },
    data: {}
  }



  signin = (e) =>{
     
      e.preventDefault()
      
       fetch('http://127.0.0.1:8000/auth',{
        method: "POST",
        headers: {"Content-Type": "application/json"
        },
                 
        body: JSON.stringify(this.state.user)
      })
      .then(res=>res.json())
      .then(res=>{this.props.userLogin(res.token)
           
      })
      .catch(e=>console.log(e))
      

      
  }

  register=e=>{

    e.preventDefault()

       fetch('http://127.0.0.1:8000/api/admin/',{
        method: "POST",
        headers: {"Content-Type": "application/json"
        },
                 
        body: JSON.stringify(this.state.user)
      })
      .then(res=>res.json())
      .then(res=>console.log(res))
      .catch(e=>console.log(e))
  }

 

   changeHandler = e =>{
    const us = this.state.user
    us[e.target.name] = e.target.value
    this.setState({user : us})
    
  }

  render(){
    return(
      <div className="form-wrap">
        <form className="form">
          <input type="text" name="username"value={this.state.user.login} onChange={this.changeHandler} placeholder="Логин"/>
          <input type="text" name="password" value={this.state.user.password} onChange={this.changeHandler} placeholder="Пароль"/>
          <div className='action_block'>        
            <button onClick={this.signin}>Войти</button>
            <button onClick={this.register}>Зарегистрироваться</button>         
          </div>
          
        </form>
      </div>
  )
  }

  
}

export default Signin